.home {
  width: 1280px;
  height: 800px;
  padding-top: 48px;
  padding-left: 80px;

  &.pink {
    background: linear-gradient(to right,
            #FFEDF7 0%,
            #FFEDF7 830px,
            #003164 0%,
            #003164 450px);

  }

  &.blue {
    background: linear-gradient(to right,
            var(--lightBlue) 0%,
            var(--lightBlue) 830px,
            #003164 0%,
            #003164 450px);

  }
}

.layout {
  position: relative;
}


